.App {
  text-align: center;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.appbarcolor {
  background-image: linear-gradient(180deg, #907c64 8% , #F9D8A6 40%, #f5e8d6) !important;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
}

p, .color-text{
  color: #907c64;
}

.bg-header{
  background-color: #e7c976 !important;
}

.colorbase{
  color: #926f20 !important;
}

img{
  max-width:100% !important;
}

.MuiCard-root{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.text-lighting{
  color: #4d81ed !important;
}
.bg-lighting{
  background-color: #4d81ed !important;
}
.shadow{
  -webkit-box-shadow: 0 0 10px #313131 !important;
        box-shadow: 0 0 10px #313131 !important;
}
.bg-body{
  background-color: #F9D8A6 !important;
}

a:hover{
  text-decoration: none !important;
}
a{
  color:#052c81 
}
.consentBtn {
  color: #052c81 !important;
  border-color: #052c81 !important;
}
.middlecenter{
  margin: 0;
  position: absolute;
  top: 50%;
  width: 50%;
  -ms-transform: translateY(-50%);
  transform: translateY(-50%);
}



.MuiButton-containedSizeMedium {
 color: #635544 !important;
 font-size: 18px !important;
 margin: 3px !important;
 background:
   radial-gradient(circle 30px at top left ,#ffffff00  ) top left,
   radial-gradient(circle 30px at top right,#ffffff00 ) top right,
   radial-gradient(circle 30px at bottom left ,#ffffff00) bottom left,
   radial-gradient(circle 30px at bottom right,#ffffff00) bottom right !important;
 background-repeat:no-repeat;
}